import React from 'react';
import styles from './Result.module.scss';

const Result = ({temp,feelTemp,pressure,clouds,town,humidity,windSpeed}) => (
    <>
        <div className={styles.result}>
            <h1>Results for {town}</h1>
            <h2>Temperature : {temp}*C</h2>
            <h2>Feels like : {feelTemp}*C</h2>
            <h2>Pressure : {pressure} hpa</h2>
            <h2>Humidity : {humidity}%</h2>
            <h2>{clouds}</h2>
            <h2>Wind : {windSpeed}m/s</h2>
        </div>
    </>
)

export default Result;