import React from 'react';
import styles from './Main.module.scss';
import Result from './Result/Result';

class Main extends React.Component {
    state = {
        temp : null,
        feelTemp : null,
        pressure : null,
        clouds : null,
        townName : null,
        humidity : null,
        windSpeed : null
    }
    find = (e) => {
        e.preventDefault();
        fetch(`https://api.openweathermap.org/data/2.5/weather?q=${e.target[0].value}&appid=c96cdf3c37925c7c4146405b05309191`)
        .then(response => response.json())
        .then(data => {
            this.setState({temp : Math.round(data.main.temp - 273)});
            this.setState({feelTemp : Math.round(data.main.feels_like - 273)});
            this.setState({pressure : data.main.pressure});
            this.setState({clouds : data.weather[0].description});
            this.setState({townName : data.name});
            this.setState({humidity : data.main.humidity});
            this.setState({windSpeed : data.wind.speed});
            console.log(data);
        })
        .catch(err => {
            console.log(err);
            
        })
    }

    render(){
        return(
        <>
            {this.state.temp == null ? <h1 className={styles.title}>Weather App</h1> : null}
            <form onSubmit={this.find} className={this.state.temp != null ? styles.Form : styles.Form2}>
                <input required placeholder="Town" />
                <button type="submit">Search</button>
            </form>
            {this.state.temp != null ? <Result
             temp={this.state.temp} 
             feelTemp={this.state.feelTemp} 
             pressure={this.state.pressure}
             clouds={this.state.clouds}
             town={this.state.townName}
             humidity={this.state.humidity}
             windSpeed={this.state.windSpeed}
             /> 
            : null}
        </>
        )
    }
}
export default Main;